<template>
  <div class="suggestions">
    <div v-if="showTorrentBox()">
      <div>
        <button class ="button" v-if="selectedCategory > 0" @click="back">
        <i
          class="fa-solid fa-circle-left fa-fade fa-xl"
          style="color: #b197fc"
        ></i>
      </button>
      </div>
      <torrent-box
        :searchResult="paginatedSearchResult"
        :totalElement="totalElement"
        :searchText="searchText"
        :top100="true"
      ></torrent-box>
    </div>
    <div class="root">
      <div id="cards" @mousemove="mouseMove">
        <div
          class="card"
          v-for="(entry, index) in moviesResult"
          :key="index"
          @click="selectCategory(index, entry)"
        >
          <div class="card-content">
            <div class="card-image">
              <i :class="entry.class" :style="entry.style"></i>
            </div>
            <div class="card-info-wrapper">
              <div class="card-info">
                <i class="fa-solid fa-file-signature"></i>
                <div class="card-info-title">
                  <h3>{{ entry.header }}</h3>

                  <h4>Click to view</h4>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TorrentBox from "./TorrentBox.vue";

export default {
  components: { TorrentBox },
  name: "SuggestionBox",
  props: {
    top100Results: Array,
  },
  data() {
    return {
      selectedCategory: 0,
      paginatedSearchResult: [],
      searchText: "",
      totalElement: 100,
      moviesResult: this.top100Results,
    };
  },

  created() {
    //console.log(this.top100Results)
  },

  methods: {
    back() {
      window.location.reload();
    },
    selectCategory(index, data) {
      this.paginatedSearchResult = [];
      this.selectedCategory = index + 1;
      const movies = data.data;
      this.searchText = movies.header;
      movies.forEach((entry) => {
        this.paginatedSearchResult.push(entry);
      });
      this.moviesResult = [];
    },

    showTorrentBox() {
      return this.selectedCategory > 0;
    },

    mouseMove(e) {
      for (const card of document.getElementsByClassName("card")) {
        const rect = card.getBoundingClientRect(),
          x = e.clientX - rect.left,
          y = e.clientY - rect.top;
        card.style.setProperty("--mouse-x", `${x}px`);
        card.style.setProperty("--mouse-y", `${y}px`);
      }
    },
  },
};
</script>

<style scoped>
p {
  margin-top: 100px;
  font-size: 18px;
  margin-bottom: 30px;
  color: rgb(64, 101, 101);
  font-weight: 600;
}

.button {
  left:10px;
  margin-top: 42px;
  position: fixed;
  cursor: pointer;
  border: none;
}

.suggestions {
  padding-top: 20px;
  overflow: hidden;
}

.root {
  background-color: rgb(240, 240, 240);
  width: 100%;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  height: auto;
  justify-content: center;
  overflow: hidden;
}

#cards {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  max-width: 916px;
  width: calc(100% - 20px);
  position: relative;
  overflow: hidden;
}

#cards:hover > .card::after {
  opacity: 1;
}

.card {
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  cursor: pointer;
  display: flex;
  height: 260px;
  flex-direction: column;
  position: relative;
  width: 300px;
}

.card:hover::before {
  opacity: 1;
}

.card::before,
.card::after {
  border-radius: inherit;
  content: "";
  height: 100%;
  left: 0px;
  opacity: 0;
  position: absolute;
  top: 0px;
  transition: opacity 500ms;
  width: 100%;
}

.card::before {
  background: radial-gradient(
    800px circle at var(--mouse-x) var(--mouse-y),
    rgba(120, 119, 119, 0.445),
    transparent 40%
  );
  z-index: 3;
}

.card::after {
  background: radial-gradient(
    600px circle at var(--mouse-x) var(--mouse-y),
    rgba(133, 132, 132, 0.12),
    transparent 40%
  );
  z-index: 15;
}

.card > .card-content {
  background-color: rgba(20, 20, 20, 0.9);
  border-radius: inherit;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  inset: 1px;
  padding: 10px;
  position: absolute;
  z-index: 2;
}

h1,
h2,
h3,
h4,
span {
  color: rgb(240, 240, 240);
  font-family: "Rubik", sans-serif;
  font-weight: 400;
  margin: 0px;
}

i {
  color: rgb(240, 240, 240);
}

.card-image {
  align-items: center;
  display: flex;
  height: 140px;
  justify-content: center;
  overflow: hidden;
}

.card-image > i {
  font-size: 6em;
  opacity: 1;
}

.card-info-wrapper {
  align-items: center;
  display: flex;
  flex-grow: 1;
  justify-content: flex-start;
  padding: 0px 20px;
}
.card-info {
  align-items: flex-start;
  display: flex;
  gap: 10px;
}

.card-info > i {
  font-size: 1em;
  height: 20px;
  line-height: 20px;
}

.card-info-title > h3 {
  font-size: 1.1em;
  line-height: 20px;
}

.card-info-title > h4 {
  color: reba(255 255 255 0.5);
  font-size: 0.85em;
  margin-top: 8px;
}

@media (max-width: 1000px) {
  .root {
    align-items: flex-start;
    overflow: auto;
  }

  #cards {
    max-width: 1000px;
    padding: 10px 0px;
  }
  .card {
    flex-shrink: 1;
    width: calc(50% - 4px);
  }
}

@media (max-width: 500px) {
  .card {
    height: 180px;
  }

  .card-image {
    height: 80px;
  }

  .card-image > i {
    font-size: 3em;
  }
}

.card-info-wrapper i {
  padding: Opx 10px;
}

.card-info > i {
  font-size: 0.8em;
}

.card-info-title > h3 {
  font-size: 0.9em;
}

.card-info-title › h4 {
  font-size: 0.8em;
  margin-top: 4px;
}

@media (max-width: 320px) {
  .card {
    width: 100%;
  }
}

#source-link > i {
  color: rgb(94, 106, 210);
}

.link {
  align-items: center;
  backdrop-filter: blur (3px);
  background-color: Urgba(255, 255, 255, 0.05);
  border: 1px solid Urgba (255, 255, 255, 0.1);
  border-radius: 6px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  display: inline-flex;
  gap: 5px;
  left: 10px;
  padding: 10px 20px;
  position: fixed;
  text-decoration: none;
  z-index: 100;
}

.link:hover {
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.link > i,
link > span {
  height: 20px;
  line-height: 20px;
}

.link > span {
  color: white;
}
</style>