<template>
  <div class="error" v-if="isEmpty() && !top100">
    <h2>No search result found</h2>
  </div>
  <button v-if="!top100" class="button" @click="back" name="Back">
    <i
      class="fa-solid fa-circle-left fa-fade fa-xl"
      style="color: #b197fc"
    ></i>
  </button>
  <div class="full-box">
    <h5 v-if="!top100">{{ resultHeader }}</h5>
    <div class="single-box" v-for="entry in moviesData" :key="entry._id">
      <div class="first-row" v-if="top100">{{ entry.title }}</div>
      <div class="first-row" v-else>{{ entry.Name }}</div>
      <div class="second-row">
        <div class="second-row-1">
          <div class="green">
            <img
              src="../assets/leacher.svg"
              alt="Leecher"
              style="transform: translateY(-2px)"
            />
            <div>{{ entry.Leechers }}</div>
          </div>
          <div class="brown">
            <img
              src="../assets/seeder.svg"
              alt="Seeder"
              style="transform: translateY(-2px)"
            />
            <div>{{ entry.Seeders }}</div>
          </div>
          <div class="black">
            <img src="../assets/download.svg" alt="Download" />
            <div>{{ entry.Downloads }}</div>
          </div>
          <div class="cyan">
            <img
              src="../assets/size.svg"
              alt="Size"
              style="transform: translateY(-2px)"
            />
            <div>{{ entry.Size }}</div>
          </div>
          <div class="upload-date">
            <img
              src="../assets/calendar.svg"
              alt="Calendar"
              style="transform: translateY(-2px)"
            />
            <div>{{ entry.DateUploaded }}</div>
          </div>
        </div>
        <div class="second-row-2">
          <p>{{ getText(entry.UploadedBy) }}</p>
        </div>
        <div class="second-row-3">
          <div class="download-link">
            <a :href="entry.magnet"> 
              <img class="img" src="../assets/NOW.png" alt="Download"
            /></a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "TorrentBox",
  props: {
    searchResult: Array,
    totalElement: Number,
    searchText: String,
    top100: Boolean,
  },
  data() {
    return {
      resultHeader: "",
      searchStart: false,
      moviesData: this.searchResult,
    };
  },
  created() {
    this.resultHeader =
      this.totalElement + " search results found for " + this.searchText;
  },

  watch: {
    searchText() {
      this.resultHeader =
        this.totalElement + " search results found for " + this.searchText;
    },
  },
  methods: {
    back() {
      window.location.reload();
    },

    getDate(date) {
      const newDate = new Date(date);
      return newDate.toLocaleDateString();
    },

    getText(data) {
      if (data) {
        var text = "Uploaded by: " + data;

        return text;
      }

      return "Uploaded by: No data found";
    },

    getSize(number) {
      return Math.round(number / 1048576) + " MB";
    },

    isEmpty() {
      if (this.moviesData.length < 1) {
        return true;
      }

      return false;
    },
  },
};
</script>

<style scoped>
.full-box {
  position: unset;
  top: 0;
  padding-top: 50px;
  width: 100vw;
  height: auto;
  margin: auto;
  overflow: hidden;
}

.single-box {
  width: 90vw;
  height: auto;
  margin: auto;
  margin-top: 10px;
  display: grid;
  grid-template-columns: repeat(1);
  grid-template-rows: repeat(5);
  grid-gap: 40px;
  border-radius: 10px;
  background-color: #ffffff;
}

.first-row {
  grid-column: 1;
  grid-row: 1/2;
  font-family: Segoe UI, Arial, sans-serif;
  color: #566a7f;
  font-weight: 600;
  margin-top: 0;
  word-break: break-all;
  font-size: 15px;
}

.second-row {
  grid-column: 1;
  grid-row: 2;
  margin-top: -30px;
  display: grid;
  grid-template-columns: repeat(5);
  grid-template-rows: repeat(3);
  font-size: 14px;
  font-family: "Times New Roman", Times, serif;
}

.second-row-1 {
  grid-column: 1 / 6;
  grid-row: 1;
  display: contents;
  font-size: 14px;
}

.second-row-2 {
  grid-column: 1 / 6;
  grid-row: 2;
}

.second-row-3 {
  grid-column: 1 / 6;
  grid-row: 3;
  display: grid;
  grid-template-columns: repeat(3);
  font-size: 14px;
}

.green {
  grid-row: 1;
  color: rgb(0, 157, 0);
  font-weight: bold;
}

.brown {
  grid-row: 1;
  color: rgb(195, 82, 87);
  font-weight: bold;
}

.black {
  grid-row: 1;
  color: rgb(77, 126, 123);
  font-weight: bold;
}

.cyan {
  grid-row: 1;
  color: rgb(77, 126, 123);
  font-weight: bold;
}

.upload-date {
  grid-row: 1;
  color: rgb(77, 126, 123);
  font-weight: bold;
}

.download-link {
  grid-row: 1;
}

.img {
  width: 130px;
  height: auto;
}

h5 {
  margin-top: 70px;
  color: rgb(17, 96, 125);
  margin-bottom: 10px;
}

p {
  font-size: 13px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-weight: 600;
  color: rgb(17, 96, 125);
}

.error {
  color: rgb(187, 19, 19);
  font-weight: 500;
  margin-bottom: 50px;
}

.button {
  left:10px;
  margin-top: 100px;
  position: fixed;
  cursor: pointer;
  border: none;
  z-index: 35;
}

h2 {
  margin-top: 50px;
}
</style>