<template>
  <div class="search-box">
  </div>
  <form class="example" @click.prevent="onSubmit">
    <input type="text" placeholder="Type your query and enter..." name="search" v-model="searchText"/>
    <button type="submit" @click="searchWithText()"><i class="fa fa-search" style="color: black"></i></button>
  </form>
</template>

<script>
export default {
  name: "SearchBox",
  props: {
  },
  data() {
    return {
      searchText: null,
    }
  },
  methods: {
    searchWithText(){
      this.$emit("trigger-search", this.searchText);
    }
  },
  emits: ["trigger-search"],
};
</script>

<style scoped>
* {
  box-sizing: border-box;
}

form.example input[type="text"] {
  padding: 10px;
  float: left;
  font-size: 17px;
  border: 1px solid grey;
  width: 100%;
  background: #f1f1f1;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10Px;
}

form.example button {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10Px;
  float: left;
  width: 30%;
  padding: 10px;
  color: rgb(0, 0, 0);
  font-size: 17px;
  border: 1px solid rgb(188, 188, 188);
  border-left: none;
  cursor: pointer;
  background-color: #f1f1f1;
}

form.example button:hover {
  background: #0b7dda;
}

form.example {
  width: 60%;
  display: flex;
  margin: auto;
}
</style>
