<template>
  <div class="pagination">
    <input type="number" v-model="goTo" min="1" />
    <button @click="goToPage()">Go</button>
    <a class="grey" @click="clickPrevious()">&laquo;</a>
    <a @click="clickNumber(-2)" v-if="getVisibility(2)">{{ currentPageNumber - 2 }}</a>
    <a @click="clickNumber(-1)" v-if="getVisibility(1)">{{ currentPageNumber - 1 }}</a>
    <a class="active">{{ currentPageNumber }}</a>
    <a @click="clickNumber(1)">{{ currentPageNumber + 1 }}</a>
    <a @click="clickNumber(2)">{{ currentPageNumber + 2 }}</a>
    <a v-if="!getVisibility(1)" @click="clickNumber(3)">{{ currentPageNumber + 3 }}</a>
    <a class="grey" @click="clickNext()">&raquo;</a>
  </div>
</template>

<script>
export default {
  name: "PaginationBox",
  props: {
    pageNumber: Number,
    total: Number,
  },
  data() {
    return {
      goTo: 0,
      currentPageNumber: this.pageNumber,
    };
  },
  created() {
    this.goTo = this.pageNumber;
  },
  methods: {
    getVisibility(number) {
      return this.currentPageNumber > number;
    },

    goToPage() {
      if (this.total > this.goTo * 20 - 1) {
        this.$emit("trigger-page", this.goTo);
        this.currentPageNumber = this.goTo;
      }
    },

    clickNext() {
      if (this.total > 19 && this.currentPageNumber * 20 - 1 < this.total) {
        this.$emit("trigger-page", ++this.currentPageNumber);
        this.goTo = this.currentPageNumber;
      }
    },

    clickPrevious() {
      if (this.total > 19 && this.currentPageNumber > 1) {
        this.$emit("trigger-page", --this.currentPageNumber);
        this.goTo = this.currentPageNumber;
      }
    },

    clickNumber(number) {
      if (this.total > (this.currentPageNumber + number) * 20 - 1) {
        this.$emit("trigger-page", this.currentPageNumber + number);
        this.currentPageNumber = this.currentPageNumber + number;
        this.goTo = this.currentPageNumber;
      }
    },
  },
  emits: ["trigger-page"],
};
</script>

<style scoped>
* {
  box-sizing: border-box;
  background-color: #ebebeb;
}

.grey {
  background: rgb(182, 228, 235);
  font-size: 30px;
  color: blue;
  font-weight: 900;
}

.pagination {
  display: inline-block;
  margin-top: -50px;
  margin-bottom: -50px;
}

.pagination a {
  color: black;
  float: left;
  padding: 8px 16px;
  text-decoration: none;
  font-size: 14px;
}

.pagination a.active {
  background-color: #4caf50;
  color: white;
  border-radius: 5px;
}

.pagination a:hover:not(.active) {
  background-color: #9ae6af;
  border-radius: 5px;
}

.pagination input {
  color: black;
  float: left;
  text-decoration: none;
  width: 40px;
  height: 35px;
  margin-right: 4px;
  text-align: center;
  font-size: 14px;
  border-radius: 10px;
}

.pagination button {
  float: left;
  margin-right: 4px;
  text-decoration: none;
  color: black;
  height: 35px;
  border-radius: 10px;
  font-size: 14px;
}

.pagination button:hover {
  background-color: #9ae6af;
}
</style>