<template>
  <div class="header sticky">
    <nav>
      <router-link to="/">Home | </router-link>
      <router-link to="/login">Login</router-link>
    </nav>
  </div>

  <div class="content">
    <div class="top-content">
      <search-box @trigger-search="receiveEmit"></search-box>
      <pagination-box
        v-if="showPagination()"
        @trigger-page="pageEmit"
        :pageNumber="pageNumber"
        :total="totalElement"
      ></pagination-box>
      <div v-if="isLoading()" class="waiting-bar">
        <img src="../assets/64725.png" alt="Loading..." />
      </div>
    </div>
    <div class="bottom-content">
      <torrent-box
        v-if="hasTorrent()"
        :searchResult="paginatedSearchResult"
        :totalElement="totalElement"
        :searchText="searchText"
        :top100="false"
      >
      </torrent-box>
      <suggestion-box
        v-if="paginatedSearchResult.length < 1 && !isLoading()"
        :top100Results="top100Results"
      ></suggestion-box>
    </div>
  </div>
</template>

<script>
import SearchBox from "@/components/SearchBox.vue";
import TorrentBox from "@/components/TorrentBox.vue";
import PaginationBox from "@/components/PaginationBox.vue";
import axios from "axios";
import SuggestionBox from "@/components/SuggestionBox.vue";

export default {
  name: "HomeView",
  data() {
    return {
      searchText: "",
      totalElement: 0,
      searchDone: false,
      pageNumber: 1,
      paginatedSearchResult: [],
      accessTime: null,
      userData: {},
      response: null,
      serverUrl: null,
      searchStart: false,
      top100Results: [],
    };
  },
  async created() {
    this.serverUrl = "https://torrent4you.onrender.com";
    const top100 = require("../assets/Top100.json");
    const headerValues = [
      {
        header: "Movies",
        class: "fa-solid fa-clapperboard fa-2xl",
        style: "color: #63E6BE;",
      },
      {
        header: "Television",
        class: "fa-solid fa-tv",
        style: "color: #B197FC;",
      },
      {
        header: "Games",
        class: "fa-solid fa-table-tennis-paddle-ball",
        style: "color: #0091ff;",
      },
      {
        header: "Applications",
        class: "fa-solid fa-mobile-screen-button",
        style: "color: #3ee0b7;",
      },
      {
        header: "Music",
        class: "fa-solid fa-guitar",
        style: "color: #ffbb00;",
      },
      {
        header: "XXX",
        class: "fa-solid fa-masks-theater",
        style: "color: #0091ff;",
      },
      {
        header: "Anime",
        class: "fa-solid fa-dragon",
        style: "color: #50e261;",
      },
      {
        header: "Others",
        class: "fa-solid fa-feather-pointed",
        style: "color: #B197FC;",
      },
      {
        header: "Special",
        class: "fa-solid fa-glasses",
        style: "color: #FFD43B;",
      },
    ];

    headerValues.forEach((header, index) => {
      const data = {
        header: header.header,
        class: header.class,
        style: header.style,
        data: top100[index].data,
      };
      this.top100Results.push(data);
    });

    const time = new Date(Date.now());
    this.accessTime = time.toISOString();

    await axios
      .get("https://ipinfo.io?token=dc11c7feb381a3")
      .then((res) => {
        this.userData = {
          ip: res.data.ip,
          time: this.accessTime,
          loc: res.data.loc,
          org: res.data.org,
          city: res.data.city,
        };
      })
      .catch(function (error) {
        if (error.response) {
          console.log(error.response.headers);
        } else if (error.request) {
          console.log(error.request);
        } else {
          console.log(error.message);
        }
      });

    await axios
      .post(`${this.serverUrl}/save-user-info`, this.userData, {
        headers: {
          "Content-Type": "application/json",
        },
      })
      .catch(function (error) {
        if (error.response) {
          console.log(error.response.headers);
        } else if (error.request) {
          console.log(error.request);
        } else {
          console.log(error.message);
        }
      });
  },
  components: {
    SearchBox,
    TorrentBox,
    PaginationBox,
    SuggestionBox,
  },
  methods: {
    back() {
      this.paginatedSearchResult = [];
      this.pageNumber = 1;
    },

    isLoading() {
      return !this.searchDone && this.searchStart;
    },
    showPagination() {
      return this.searchDone && this.totalElement > 20;
    },

    async pageEmit(pageNumber) {
      this.searchStart = true;
      this.searchDone = false;
      try {
        await axios
          .get(
            `${
              this.serverUrl
            }/search-by-page?searchText=${this.searchText.toLowerCase()}&pageNumber=${pageNumber}`
          )
          .then((res) => {
            const movies = res.data;

            if (movies && movies.length > 0) {
              this.paginatedSearchResult = [];
              movies.forEach((element) => {
                this.paginatedSearchResult.push(element);
              });

              this.pageNumber = pageNumber;
            }
          });
      } catch (error) {
        console.error("Error fetching API data:", error.message);
      }
      this.searchStart = false;
      this.searchDone = true;
    },

    hasTorrent() {
      return this.searchDone;
    },

    async receiveEmit(searchText) {
      if (searchText === null) {
        return;
      }
      this.searchStart = true;
      this.searchText = searchText;

      const data = {
        ip: this.userData.ip,
        searchText: this.searchText,
      };

      await axios
        .post(`${this.serverUrl}/save-search-info`, data, {
          headers: {
            "Content-Type": "application/json",
          },
        })
        .catch(function (error) {
          if (error.response) {
            console.log(error.response.headers);
          } else if (error.request) {
            console.log(error.request);
          } else {
            console.log(error.message);
          }
        });

      try {
        this.searchDone = false;
        this.paginatedSearchResult = [];
        this.pageNumber = 1;
        this.searchResult = [];

        await axios
          .get(
            `${
              this.serverUrl
            }/search-first-page?searchText=${searchText.toLowerCase()}&pageNumber=${
              this.pageNumber
            }`
          )
          .then((res) => {
            const data = res.data;
            const movies = data.results;

            if (movies && movies.length > 0) {
              movies.forEach((element) => {
                this.paginatedSearchResult.push(element);
              });
            }

            if (parseInt(res.data.hits) * 20 <= 1) {
              this.totalElement = movies.length;
            } else {
              this.totalElement = parseInt(res.data.hits) * 20;
            }
          });
      } catch (error) {
        this.searchStart = false;
        console.error("Error fetching API data:", error.message);
      }
      this.searchStart = false;
      this.searchDone = true;
    },

    async getSuggest(suggestWords) {
      for (let index = 0; index < suggestWords.size; index++) {
        this.getSearch(suggestWords[index]);
      }
    },

    async getSearch(suggestWord) {
      await axios
        .get(`${this.serverUrl}/suggest?searchText=${suggestWord}`, {
          headers: {
            "Access-Control-Allow-Origin": "*",
          },
        })
        .then(function (res) {
          this.suggestTorrentz = res.data;
        })
        .catch(function (error) {
          if (error.response) {
            console.log(error.response.headers);
          } else if (error.request) {
            console.log(error.request);
          } else {
            console.log(error.message);
          }
        });
    },
  },
};
</script>

<style>
.waiting-bar img {
  margin-top: 50px;
  border-radius: 50%;
  width: 50px;
  height: auto;
  animation: rotation 1.1s infinite ease, color 1.1s infinite ease;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes color {
  0% {
    background: cyan;
  }
  100% {
    background: violet;
  }
}

.top-content {
  position: fixed;
  z-index: 29;
  background-color: none;
  width: 100%;
  top: 0;
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: rgb(240, 240, 240);
}

.bottom-content {
  top: 0;
  position: relative;
  background-color: rgb(240, 240, 240);
  width: 100%;
  z-index: 28;
  padding-top: 100px;
}

.top-content-button {
  top: 0;
  padding-top: 50px;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
  ;
}

.header {
  background-color: rgb(50, 50, 50);
  z-index: 30;
  position: fixed;
  width: 100%;
}

.content {
  position: relative;
  width: 100%
}
</style>